.navbar{
  width: 100%;
  font-family:"Poppins", sans-serif;
  background-color: hsl(0, 0%, 100%);
  padding: 10px 20px;
  display: flex;
  align-items: center;
  top: 0;
  left: 0;
  position: fixed;
  z-index: 10;
  box-sizing: border-box;

}
.logo{
  width: 100px;
  height: auto;
}

.navitems{
  display: flex;
  justify-content: center;
  align-items: center;
}  


.navbar-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;

}

.navbar-item {
  margin: 0 15px;
}

.navbar-item a {
  color: black;
  text-decoration: none;
  border-bottom: none;
  padding: 8px;
  cursor: pointer;
  border-radius: 10px;
  display: block;
}



.navbar-item .active{
  background-color: #000;
  color: #fff;
  /* border-bottom: solid 2px black; */
}

.navbar-item a:hover {
  background-color: #000;
  color: #fff;
  /* border-bottom: solid 2px black; */
}

.navlink a{
  text-decoration: none;
  border-bottom: none;
  color: #000;
  padding: 8px 20px;
}

.navlink a:hover{
  background-color: #000;
  color: #fff;
  /* transition: background-color 1s; */
  cursor: pointer;
  border-radius: 10px;
}

.menu-icon {
  display: none;
  cursor: pointer;
  position: absolute;
  right: 20px;
}


@media (max-width: 768px) {
  .navitems {
      display: none;
  }

  .navbar {
      justify-content: space-between;

  }

  .menu-icon {
      display: block;
  }

  .navitems.open {
      display: flex;
      flex-direction: column;
      align-items: center;
      position: absolute;
      top: 60px;
      right: 10px;
      left: 10px;
      background-color: hsl(0, 0%, 100%);
      box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
      border-radius: 10px;
      z-index: 9;
      margin: 0 auto;
  }

  .navbar-list {
      flex-direction: column;
      align-items: center;
      width: 100%;
      padding: 0;
  }

  .navbar-item {
      margin: 10px 0;
      width: 100%;
      text-align: center;
  }

  .navbar-item a {
      display: block;
      width: 100%;
      padding: 10px;
  }
}

@media (max-width: 1200px) {
  .navbar {
      padding: 10px 15px;
  }

  .navbar-item {
      margin: 0px 10px;
  }

  .logo {
      width: 70px;
  }
  .navitems {
    margin-left: 100px; /* Add margin to create gap between logo and nav items */
}
}

@media (min-width: 1201px) {
  .navbar {
      padding: 10px 15px;
  }

  .navbar-item {
      margin: 0px 15px;
  }

  .logo {
      width: 100px;
  }
  .navitems {
    margin-left: 300px; /* Add margin to create gap between logo and nav items */
}
}


@media (min-width: 992px ) and (max-width:1439px) 
 {

  .navbar {
      padding: 10px 10px;
  }

  .navbar-item {
      margin: 0 8px;
  }

  .logo {
      width: 60px;
  }
  .navitems {
    margin-left: 250px; /* Adjust the gap for smaller laptops */
}

}